import { inviteTypes} from './invite.types'


export const setNewValueForInviteStoreItem = ( item, itemValue, id ) => ({
    type: inviteTypes.SET_NEW_VALUE_INVITE_FOR_STORE_ITEM,
    payload: {item, itemValue, id}
})

export const createInviteItem = (object) => ({
    type: inviteTypes.CREATE_INVITE_ITEM,
    payload: object
})

export const deleteInviteItem = (id) => ({
    type: inviteTypes.DELETE_INVITE_ITEM,
    payload: id
})