import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import {
    setNewValueForInviteStoreItem,
    createInviteItem,
    deleteInviteItem
} from "../../../../redux/licenses/corporate/invite/invite.actions";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';

const CorporateInvite = (props) => {


    const addHandler = () => {
        const reducerStore =  props.inviteData.store;
        const reducerDefData =  props.inviteData.defValues;
        let ticketID = + new Date();


        let newObject = {
            login: reducerStore[reducerStore.length-1].login + 1,
            newLogin: reducerStore[reducerStore.length-1].login + 1,
            password: reducerDefData.password,
            nickname: reducerDefData.nickname,
            telegram: reducerDefData.telegram,
            comment: reducerDefData.comment,
            ticketID: ticketID,
        };

        props.createInviteItem(newObject);
    }


    const getRowItem = (itemData) => {
        return (
            <div className="generateCard">
                <span className="color-grey">Login</span>
                <TextField
                    fullWidth
                    value={'CU' + itemData.login}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('login', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                    disabled
                />

                <span className="color-grey">New login</span>
                <TextField
                    fullWidth
                    value={'CU' + itemData.newLogin}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('newLogin', e.target.value, itemData.ticketID)}}
                    variant="filled"
                    disabled
                />

                <span className="color-grey">Password</span>
                <TextField
                    fullWidth
                    value={itemData.password}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('password', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Nickname</span>
                <TextField
                    fullWidth
                    value={itemData.nickname}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('nickname', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Telegram</span>
                <TextField
                    fullWidth
                    value={itemData.telegram}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('telegram', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Comment</span>
                <TextField
                    fullWidth
                    value={itemData.comment}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('comment', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <Button variant="contained" className="btn btn--red mb-15">
                    Exclude
                </Button>
            </div>
        )
    }




    return (
        <div className="ÑustomStyle">
            <div className="alertPanel alertPanel--mobile">
                <p className="color-white">The users listed below will be invited to join your license. You can only invite users with whom you have an agreement on migration.</p>
                <span className="btn-text color-blue">MORE</span>
            </div>
            <div className="generateItems__wrapper">
                    {props.inviteData.store.map(itemData => {
                        return getRowItem(itemData);
                    })}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    inviteData: state.corporateLicenseInvite.invite,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewValueForInviteStoreItem: (item, itemValue, id) => dispatch(setNewValueForInviteStoreItem(item, itemValue, id)),
        createInviteItem: (object) => dispatch(createInviteItem(object)),
        deleteInviteItem: ( id ) => dispatch(deleteInviteItem( id )),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateInvite);